/*Basic list*/

var React = require('react');
var PureRenderMixin = require('react/addons').addons.PureRenderMixin;
var Item=require('./singleListItem.jsx');
var _=require('lodash');
var CommonsMixins=require('./commonsMixins.jsx');
var KeywordSearch=require('./keywordSearch.jsx');
var SelectAllNone=require('./allNoneSelector.jsx');
var SelectionCounter = require('./selectionCounter.jsx');
var CustomCheckbox = require('../commons/customCheckbox.jsx');

module.exports = React.createClass({displayName: "exports",

  mixins: [CommonsMixins, PureRenderMixin],

  componentDidUpdate: function(){
    $(this.getDOMNode()).find('.filter-list-container').mCustomScrollbar({theme:"inset-dark"});
    this.props.onChangeCounter(this.props.param, _.filter(this.state.items, function(it){return it.selected}).length, this.state.items.length);    
  },

  getInitialState: function() {
    return {
      showOnlySelected: false,
      items: [],
      selected: [],
      filter: '',
    };
  },

  _getItemList: function() {
    var self = this;
    var list = [];
    if (this.props.searchAndSelectMode){
      return _.filter(this.state.items, function(it){return ((!it.hide)  || (self.state.showOnlySelected && it.selected))});
    } else {
      return this.state.items;
    }  
  },

  render: function() {
    var itemList = this._getItemList();
    var showOnlySelected = this.props.searchAndSelectMode? false : this.state.showOnlySelected;
    var noResults = "";
    if (this.props.searchAndSelectMode) {
      if (itemList.length==0){
        noResults = React.createElement("div", {className: "filter-no-results"}, React.createElement("br", null), React.createElement(Message, {message: "filters.performSearch"}))
      }
    } else {
      if (this.state.noResults){
        noResults = React.createElement("div", {className: "filter-no-results"}, React.createElement("br", null), React.createElement(Message, {message: "filters.noResults"}))
      }
    }
    if (this.props.active){
      return ( 
        React.createElement("div", {className: "tab-content"}, 
          React.createElement("div", {className: "tab-pane fade active in"}, 
            React.createElement("div", {className: "filter-group-panel selected"}, 
              
              React.createElement("div", {className: "filter-group-panel-header"}, 
                React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: this.props.label})), 
                React.createElement("div", {className: "show-selected"}, 
                  React.createElement("span", null, React.createElement(CustomCheckbox, {onChange: this._onShowSelectedClicked})), 
                  React.createElement("span", null, React.createElement(Message, {message: "filters.showOnlySelected"}))
                ), 
                React.createElement(SelectAllNone, {onSelectAll: this._onSelectAll, onSelectNone: this._onSelectNone})
              ), 
              React.createElement(KeywordSearch, {onSearch: this._onSearch, 
                lengthLimit: this.props.searchAndSelectMode? "5" : "", 
                onSearchEnterKey: this.props.searchAndSelectMode? "" : this._onSearchEnterKey, 
                onClear: this._onSearchClear}), 
              noResults, 
              React.createElement("div", {className: "filter-list-container"}, 
                React.createElement("ul", {className: "filter-list"}, 
                
                  itemList.map(function(item) {
                    return (React.createElement("li", null, 
                        React.createElement(Item, React.__spread({},  item, 
                          {onItemChange: this._onItemChange, 
                          showOnlySelected: this.state.showOnlySelected, 
                          searchAndSelectMode: this.props.searchAndSelectMode}))
                      ))                    
                  },this) 
                
                )
              )
            )
          )
        )

        );
    } else {
      return null;
    }
  }

});