'use strict';

var React = require('react');
var Reflux = require('reflux');
var DatePicker=require('.../../../commons/datePicker.jsx');
var _=require('lodash');

var DateFormat = 'dd/mm/yy';

var DateFilter = React.createClass({displayName: "DateFilter",

    onStatusChange: function(status, selection) {
        if(status.loadedFromSaved){
            this._setStartDate(status.sd);
            this._setEndDate(status.ed);
        } else {
            this.setState(_.clone(status)); 
        }
    },

    _setStartDate: function(date){
        //this.setState({'startDate': date});
        this.actions.updateItemValue('sd', date);
        if (!this.state.ed && date!=''){
            var d = $.datepicker.parseDate(DateFormat, date);
            d.setFullYear(d.getFullYear() + 1);
            var dFormated = $.datepicker.formatDate(DateFormat,d)
            //this.setState({'endDate': dFormated});
            this.actions.updateItemValue('ed', dFormated);
        }
    },    

    _setEndDate: function(date){
        //this.setState({'endDate': date});
        this.actions.updateItemValue('ed', date);
        if (!this.state.sd && date!=''){
            var d = $.datepicker.parseDate(DateFormat, date);
            d.setFullYear(d.getFullYear() - 1);
            var dFormated = $.datepicker.formatDate(DateFormat,d)
            //this.setState({'startDate': dFormated});
            this.actions.updateItemValue('sd', dFormated);
        }
    },

    _clearDates: function(){
        //this.setState({'endDate': '', 'startDate': ''});
        this.actions.updateItemValue('sd', "");
        this.actions.updateItemValue('ed', "");
    },    

    componentDidMount: function(){
        this.actions = this.props.actions;
        this.unsubscribe = this.props.store.listen(this.onStatusChange);        
    },   

    componentWillUnmount: function() {
        this.unsubscribe();
    },

    getInitialState: function() {
        return {
            startDate: '',
            endDate: ''
        };
    },

    render: function() {  
        console.log('DateFilter -> render');
        if (this.props.active){
            return(
                React.createElement("div", {className: "tab-content"}, 
                    React.createElement("div", {className: "tab-pane fade active in"}, 
                        React.createElement("div", {className: "filter-group-panel selected"}, 
                            React.createElement("div", {className: "filter-group-panel-header"}, 
                                React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: "filters.subActivitiesBetween"}))
                            ), 
                            React.createElement("div", {className: "input-group date"}, 
                                React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: "filters.startDate"})), 
                                React.createElement(DatePicker, {dateFormat: DateFormat, maxDate: this.state.ed, defaultDate: this.state.sd, onChange: this._setStartDate})
                            ), 
                            React.createElement("div", {className: "input-group date"}, 
                                React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: "filters.endDate"})), 
                                React.createElement(DatePicker, {dateFormat: DateFormat, minDate: this.state.sd, defaultDate: this.state.ed, onChange: this._setEndDate})
                            ), 
                            React.createElement("div", {className: "input-group date"}, 
                                React.createElement("button", {type: "button", className: "btn btn-apply clear-dates", role: "button", onClick: this._clearDates}, React.createElement(Message, {message: "filters.clearDates"}))
                            )
                        )
                    )
                )
            );  
        } else {
            return null;
        }
    }
});

module.exports = DateFilter;