var React = require('react');
var Reflux = require('reflux');
var If=require('../../../commons/if.jsx');
var LegendSymbol=require('../esri/esriSymbols.jsx');


module.exports=React.createClass({displayName: "exports",

  getInitialState: function() {
    this.state = this.state || {'expanded': false};
    return this.state;
  },

  _toggleVisibility: function(){
    this.setState({'expanded': !this.state.expanded});
  },

  render:function(){
    if (this.props.visible) {
      return (React.createElement("div", null, 
                React.createElement("div", {className: "legend-group-title", onClick: this._toggleVisibility}, 
                  React.createElement("i", {className: this.state.expanded? "fa fa-sort-asc" : "fa fa-sort-desc"}), 
                  React.createElement("span", null, React.createElement(Message, {message: this.props.layerTitle}))
                ), 
                React.createElement(If, {condition: this.state.expanded}, 
                  React.createElement("div", {className: "legend-group"}, 
                    React.createElement("ul", null, 
                    
                      this.props.legendGroups.map(function(legendGroup){
                        return (
                          React.createElement("li", null, 
                            React.createElement("h2", null, React.createElement(Message, {message: legendGroup.layerName})), 
                            React.createElement("div", {className: "legends-list"}, 
                              React.createElement("ul", null, 
                              
                                legendGroup.legends.map(function(legend){
                                  var image = "";
                                  

                                  if (legend.imageData || (legend.symbol && legend.symbol.imageData)){
                                    var imgData = legend.imageData? legend.imageData : legend.symbol.imageData;
                                    var imgColor = legend.imageColor? legend.imageColor : "#FFFFFF";
                                    var cntType = legend.contentType? legend.contentType : legend.symbol.contentType;
                                    var src = "data:"+cntType+";base64,"+imgData;
                                    image = React.createElement("img", {src: src, style: {backgroundColor:imgColor}});
                                  } else {
                                    image = React.createElement(LegendSymbol, {symbol: legend.symbol})
                                  }

                                  
                                  return (
                                    React.createElement("li", null, 
                                      image, 
                                      legend.label
                                    )
                                  );
                                })
                              
                              )
                            )
                          )
                        );
                      })
                    
                    )
                  )
                )
              ))
    } else {

      return React.createElement("div", null);
    }



  }

});