'use strict';

var React = require('react');
var Reflux = require('reflux');
var If=require('../commons/if.jsx');
var LanStore=require('../../stores/lanStore.js');
var Store=require('../../stores/saveStore.js');
var Actions=require('../../actions/saveActions.js');
var Grid=require('react-bootstrap/lib/Grid');
var Row=require('react-bootstrap/lib/Row');
var Col=require('react-bootstrap/lib/Col');
var Panel=require('react-bootstrap/lib/Panel');
var Label=require('react-bootstrap/lib/Label');
var Tooltip=require('react-bootstrap/lib/Tooltip');
var _=require('lodash');
var PrintDialog=require('./printDialog.jsx');
var ImageDialog=require('./imageDialog.jsx');

var SaveMap=require('./saveOrExportDialog.jsx');
var Modal=require('react-bootstrap/lib/Modal');
var Button=require('react-bootstrap/lib/Button');
var Pagination=require('react-bootstrap/lib/Pagination');


module.exports  = React.createClass({displayName: "exports",

mixins: [Reflux.connect(Store,"store"), Reflux.connect(LanStore, 'lan')],

  componentDidMount:function(){
    Actions.findMaps();
  },

  _open:function(id){
    Actions.openMap(id);
  },

  _update:function(id){
    //Actions.openMap(id);
    Actions.showModal('update', id)
  },

  _showDeleteModal:function(isVisible, id){
    Actions.showDeleteModal(isVisible, id);
  },

  _delete:function(){
    Actions.deleteMap();
  },

  _onKeyUp: function(ev) {
    if (ev.keyCode == 13){
      this.handleClick();
    }
  },

  handleClick:function(){
    var value = this.refs.search_input.getDOMNode().value;
    Actions.filterByKeyword(value);
  },

  handlePageSelect:function(event, selectedEvent){
    this.setState({
      activePage: selectedEvent.eventKey
    });
  },
  

  getInitialState:function(){
    return {pageSize:3, activePage:1, showDownload:false}
  },

  render: function() {
    var origMapList=this.state.store.maps || [];
    var mapList=_.sortBy(_.union(_.filter(origMapList, {hide:undefined}), _.filter(origMapList, {hide:false})), 
      function(n){return n.title.toLowerCase()});
    var showDeleteModal=this.state.store.showDeleteModal || false;
    var itemsSize=mapList.length>this.state.pageSize?Math.ceil(mapList.length/this.state.pageSize):1;

    return (

      React.createElement("div", {className: "saved-maps"}, 
      React.createElement("div", {className: "text-search-wrapper"}, 
      React.createElement("div", {className: "search-box"}, 
      React.createElement("button", {type: "submit", className: "search-button", onClick: this.handleClick}, 
      React.createElement("i", {className: "fa fa-search"})
      ), 
      React.createElement("input", {onKeyUp: this._onKeyUp, className: "keyword-search", type: "text", placeholder: i18n.t("savemap.savedmapssearch"), ref: "search_input"})
      )
      ), 
      React.createElement("h3", null, React.createElement(Message, {message: "savemap.savedmapstitle"})), 
      React.createElement("ul", null, 
        
          _.map(mapList,function(m, i){
            var lowerBound = (this.state.activePage-1) * 3;
            var upperBound = this.state.activePage * 3;
            if(i>=lowerBound && i<upperBound ){
                return (
                  React.createElement("li", {className: "saved-map-list"}, 
                    React.createElement(Grid, {fluid: true}, 
                      React.createElement(Row, null, 
                        React.createElement(Col, {sm: 8}, 
                            React.createElement("h4", {className: "pull-left"}, m.title)
                        ), 

                        React.createElement(Col, {sm: 4}, 
                            React.createElement("div", {className: "save-map-actions pull-right"}, 
                              React.createElement("a", {href: "#"}, 
                              React.createElement("i", {className: "pull-right fa fa-times", title: i18n.t('savemap.tooltipdelete'), onClick: this._showDeleteModal.bind(this, true, m._id)})
                              ), 
                              React.createElement("a", {href: "#"}, 
                              React.createElement("i", {className: "pull-right fa fa-pencil", title: i18n.t('savemap.tooltipupdate'), onClick: this._update.bind(this,m._id)})
                              ), 
                              React.createElement(PrintDialog, {key: m.id, id: m._id}), 
                              React.createElement(ImageDialog, {key: m.id, id: m._id}), 
                              React.createElement("a", {href: "#"}, 
                              React.createElement("i", {className: "pull-right fa fa-folder-open", title: i18n.t('savemap.tooltipopen'), onClick: this._open.bind(this,m._id)})
                              )
                          )
                        )
                      )
                      ), 

                      React.createElement(Grid, {fluid: true}, 
                      React.createElement(Row, null, 
                        React.createElement(Col, {md: 12}, 
                        React.createElement(Panel, {className: "pull-left"}, 
                          m.description
                        )
                        )
                      ), 
                      React.createElement(Row, null, 
                        React.createElement(Col, {md: 12}, 
                        React.createElement(Panel, {className: "pull-left"}, 
                          
                            _.map(m.tags,function(t){
                              return (React.createElement("span", {className: "label-tag"}, t) )
                            })
                          
                        )
                        )
                      )
                    )

                ))
            }
          }.bind(this))

        
      ), 
      React.createElement("div", null, 
        React.createElement(If, {condition: mapList.length>0}, 
          React.createElement(Pagination, {bsSize: "small", items: itemsSize, activePage: this.state.activePage, onSelect: this.handlePageSelect})
        )
      ), 
      React.createElement(Modal, React.__spread({className: "dialog-save-map"},  this.props, {bsSize: "medium", "aria-labelledby": "contained-modal-title-lg", 
       show: showDeleteModal, onHide: this._showDeleteModal.bind(this, false)}), 
        React.createElement(Modal.Header, null, 
          React.createElement(Modal.Title, null, React.createElement(Message, {message: "savemap.confirmation"})), 
          React.createElement("a", {className: "close-dialog", href: "#", onClick: this._showDeleteModal.bind(this, false)}, 
          React.createElement("i", {className: "fa fa-times-circle-o"}))
        ), 
        React.createElement(Modal.Body, null, 
          React.createElement("div", {className: "plain-panel"}, React.createElement(Message, {message: "savemap.areyousure"}))
        ), 
        React.createElement(Modal.Footer, null, 
          React.createElement(Button, {className: "btn btn-apply pull-right", onClick: this._delete.bind(this)}, React.createElement(Message, {message: "savemap.yes"})), 
          React.createElement(Button, {className: "pull-right", onClick: this._showDeleteModal.bind(this, false)}, React.createElement(Message, {message: "savemap.no"}))
        )
      )
      )
      );
  }

});
