'use strict';
var React = require('react/addons');
var Reflux = require('reflux');
var ArcgisLayerStore = require('../../../stores/arcgisLayerStore.js');
var ArcgisLayerActions = require('../../../actions/arcgisLayersActions.js')
var _=require('lodash')
var Toggler=require('../../commons/toggler.jsx').Toggler;
var TogglerContent=require('../../commons/toggler.jsx').TogglerContent;
var If=require('../../commons/if.jsx');
var LanStore=require('../../../stores/lanStore.js');


var Layer=require('./_layer.jsx');
var InnerLayer=require('./_innerLayer.jsx');

var FeatureLayer=React.createClass({displayName: "FeatureLayer",

  render:function(){
    var onChangeOpacity=this.props.onChangeOpacity;
    var onChangeVisibility=this.props.onChangeVisibility;
    var id=this.props.id;
    var defaultVisibility=this.props.visible;

    return (
      React.createElement("div", null, 
        React.createElement(Layer, {onDelete: this.props.onDelete, 
          onRemove: this.props.onRemove, 
          opacity: this.props.opacity, 
          visible: this.props.visible, 
          showBasicControl: this.props.showBasicControl, 
          title: this.props.title, 
          onChangeVisibility: this.props.onChangeVisibility, 
          onChangeOpacity: onChangeOpacity, 
          hideTransparencyControl: this.props.layer.layers.length>1? false : true, 
          id: this.props.id}), 
        React.createElement(If, {condition: !this.props.showBasicControl}, 
          React.createElement("ul", {style: {'paddingLeft': '20px'}}, 
            
              this.props.layer.layers.map(function(l){
                return (React.createElement("li", null, React.createElement(InnerLayer, {
                opacity: l.opacity, 
                visible: l.visible, 
                onChangeOpacity: onChangeOpacity, 
                onChangeVisibility: onChangeVisibility, title: l.name, id: id, idx: l.id.toString()})))
              }.bind(this))
            
          )
        )
      )
    )
  }
});

module.exports  = React.createClass({displayName: "exports",
  mixins: [Reflux.listenTo(ArcgisLayerStore,"_onStatusChange"), Reflux.connect(LanStore, 'lan')],

  _onStatusChange: function(status) {
    this.setState(status); //create a new array in order to isolate the state
  },

  getInitialState: function() {
    return {}; //{layers:ArcgisLayerStore.getInitialState().leafletLayers.slice(0)};
  },

  _changevisibility: function(id, value,idx) {
    ArcgisLayerActions.changeLayerValue('visible', id, value,idx); 
  },

  _handleChangeOpacity: function(id, value,idx) {
    ArcgisLayerActions.changeLayerValue('opacity', id, value,idx); 
  },

  _handleMoveUp: function(id) {
    ArcgisLayerActions.changeLayerValue('moveUp', id);
  },

  _handleMoveDown: function(id) {
    ArcgisLayerActions.changeLayerValue('moveDown', id); 
  },

  _onDelete: function(id) {
    ArcgisLayerActions.changeLayerValue('delete', id); 
  },


  render: function() {
    var tiles=_.sortBy(_.filter(this.state.layers,{type:'Map Service'}),'zIndex').reverse();
    var features=_.sortBy(_.filter(this.state.layers,{type:'Feature Service'}),'zIndex').reverse();

    return (
    React.createElement("div", null, 
      React.createElement(If, {condition: features.length > 0}, 
        React.createElement("div", {className: "layer-control-toggle"}, 
          React.createElement("h3", null, "Overlays")
        ), 
        React.createElement("div", {className: "layer-control-panel"}, 
          React.createElement("ul", {className: "layer-control"}, 
          
            features.map(function(l){
            return (
                React.createElement("li", {className: "external-layer-control"}, 
                  React.createElement(Toggler, {ref: "toggler"}, 
                    React.createElement(TogglerContent, {visibleWhen: "collapsed"}, 
                      React.createElement("div", {toggler: true, className: "toggler-button"}, React.createElement("i", {className: "fa fa-chevron-down"}))
                    ), 
                    React.createElement(TogglerContent, {visibleWhen: "expanded"}, 
                      React.createElement("div", {toggler: true, className: "toggler-button"}, React.createElement("i", {className: "fa fa-chevron-up"}))
                    ), 
                    React.createElement(TogglerContent, {visibleWhen: "collapsed"}, 
                      React.createElement(FeatureLayer, {
                        onDelete: this._onDelete, 
                        onChangeOpacity: this._handleChangeOpacity, 
                        onChangeVisibility: this._changevisibility, 
                        id: l.id, type: l.type, 
                        zIndex: l.zIndex, 
                        visible: l.visible, 
                        layer: l.layer, 
                        opacity: l.opacity, 
                        title: l.title, 
                        showBasicControl: true})
                    ), 
                    React.createElement(TogglerContent, {visibleWhen: "expanded"}, 
                      React.createElement(FeatureLayer, {
                        onDelete: this._onDelete, 
                        onChangeOpacity: this._handleChangeOpacity, 
                        onChangeVisibility: this._changevisibility, 
                        id: l.id, type: l.type, 
                        zIndex: l.zIndex, 
                        visible: l.visible, 
                        layer: l.layer, 
                        opacity: l.opacity, 
                        title: l.title})
                    )
                  )
                )
              )
             }.bind(this))
          
          )
        )
      ), 

      React.createElement(If, {condition: tiles.length > 0}, 
        React.createElement("div", {className: "layer-control-toggle"}, 
          React.createElement("h3", null, "Tiles")
        ), 
        React.createElement("div", {className: "layer-control-panel"}, 
          React.createElement("ul", {className: "layer-control"}, 
          
            tiles.map(function(l){
             return (
              React.createElement("li", null, 
                React.createElement(Toggler, {ref: "toggler"}, 
                  React.createElement(TogglerContent, {visibleWhen: "collapsed"}, 
                    React.createElement("div", {toggler: true, className: "toggler-button"}, React.createElement("i", {className: "fa fa-chevron-down"}))
                  ), 
                  React.createElement(TogglerContent, {visibleWhen: "expanded"}, 
                    React.createElement("div", {toggler: true, className: "toggler-button"}, React.createElement("i", {className: "fa fa-chevron-up"}))
                  ), 
                  React.createElement(TogglerContent, {visibleWhen: "collapsed"}, 
                    React.createElement(Layer, {
                      onDelete: this._onDelete, 
                      onMoveUp: this._handleMoveUp, 
                      onMoveDown: this._handleMoveDown, 
                      onChangeOpacity: this._handleChangeOpacity, 
                      onChangeVisibility: this._changevisibility, 
                      id: l.id, 
                      type: l.type, 
                      zIndex: l.zIndex, 
                      visible: l.visible, 
                      opacity: l.opacity, 
                      title: l.title, 
                      showBasicControl: true})
                  ), 
                  React.createElement(TogglerContent, {visibleWhen: "expanded"}, 
                    React.createElement(Layer, {
                      onDelete: this._onDelete, 
                      onMoveUp: this._handleMoveUp, 
                      onMoveDown: this._handleMoveDown, 
                      onChangeOpacity: this._handleChangeOpacity, 
                      onChangeVisibility: this._changevisibility, 
                      id: l.id, 
                      type: l.type, 
                      zIndex: l.zIndex, 
                      visible: l.visible, 
                      title: l.title, 
                      opacity: l.opacity})
                  )
                )
              ))
           }.bind(this))
          
          )
        )
      )
    )
  );
}});
