var React = require('react');
var Reflux = require('reflux');
var SingleList=require('./singleList.jsx');
var TreeList=require('./treeList.jsx');
var DateFilter=require('./dateFilter.jsx');
var ValueRangeFilter=require('./valueRangeFilter.jsx');
var MultiLevelFilter=require('./multiLevelSearchFilter.jsx');

module.exports = React.createClass({displayName: "exports",

 render: function() {
  var content;
  switch(this.props.type) {
    case 'list':
      content = React.createElement(SingleList, React.__spread({},  this.props))
      break;
    case 'tree':
      content = React.createElement(TreeList, React.__spread({},  this.props))
      break;
    case 'date':
      content = React.createElement(DateFilter, React.__spread({},  this.props))
      break;
    case 'valueRange':
      content = React.createElement(ValueRangeFilter, React.__spread({},  this.props))
      break;
    case 'multiLevelSearch':
      content = React.createElement(MultiLevelFilter, React.__spread({},  this.props))
      break;
  }

  return(React.createElement("div", null, content));    
}
});



