'use strict';

var React = require('react');
var Reflux = require('reflux');


var AddButton= React.createClass({displayName: "AddButton",
	handleAdd:function(){
		this.props.onAddLayer(); //add current service to map
	},

	render: function() {
		if (this.props.error){
			return (React.createElement("div", {className: "btn btn-xs btn-danger"}, "error"));
		}else if (this.props.loading){
			return (React.createElement("div", {className: "btn btn-xs btn-warning"}, "loading"));
		}
		else if (this.props.loaded){
			return (React.createElement("div", {className: "btn btn-xs btn-default"}, "added"))
		}else{
			return (React.createElement("button", {disabled: this.props.disabled, className: this.props.disabled?"btn btn-xs btn-default":"btn btn-apply", 
			 onClick: this.handleAdd}, React.createElement(Message, {message: "layers.add"})))
		}
	}
});


var ResultRecord=React.createClass({displayName: "ResultRecord",

	_handleAdd:function(){

		this.props.onAddLayer({'id':this.props.id,'url':this.props.url,'type':this.props.type,title:this.props.title})
	},

	componentDidMount: function(){
        $(this.getDOMNode()).find('.title').tooltip({container: 'body'});
        $(this.getDOMNode()).find('.details').mCustomScrollbar({theme:"inset-dark"}).tooltip({container: 'body'});    
    },

	render: function() {
		console.log("layers->search->resultList: Render EsriService");
		return(
				React.createElement("div", {className: "esri-result-item"}, 
					React.createElement("div", {className: "layer-wrapper"}, 
						React.createElement("div", {className: "thumbnail pull-left"}, 
							React.createElement("img", {width: "110px", height: "73px", src: "http://www.arcgis.com/sharing/content/items/"+this.props.id+"/info/"+this.props.thumbnail+  (this.props.token?"?token="+this.props.token:"") })
						), 
						React.createElement("div", {className: "layer-info"}, 
							React.createElement("div", {className: "title", "data-toggle": "Loging is required", title: this.props.title}, this.props.title, " ", this.props.loginRequired?React.createElement("i", {className: "text-warning small"}, "Loing is required"):''
							), 

							React.createElement("div", {className: "details", title: this.props.snippet}, this.props.snippet), 
							React.createElement("div", {className: "details small"}, this.props.type, "  - ", this.props.access), 
							React.createElement("div", {className: "add"}, 
								React.createElement(AddButton, {className: "btn btn-apply", 
									onAddLayer: this._handleAdd, 
									loading: this.props.loading, 
									error: this.props.error, 
									loaded: this.props.loaded, 
									disabled: ((this.props.loginRequired && !this.props.token)|| this.props.loaded)?true:false})
							)
						)
					)
				)
			)
	}

});

//default view
module.exports=React.createClass({displayName: "exports",

	componentDidUpdate: function(){
		//$(this.getDOMNode()).find('.esri-result-list').mCustomScrollbar({axis:"x", theme:"inset-dark"});    
    },

	render: function() {
		console.log("layers->search->resultList: Render EsriServiceList");
		var errorMessage = "";
		if(this.props.error){
			errorMessage = this.props.error.message;
		} 
		return(
			React.createElement("div", null, 
				(errorMessage && errorMessage!="")?
					React.createElement("div", null, 
						React.createElement("p", {className: "label label label-danger"}, errorMessage), 
						React.createElement("hr", {className: "h-divider"})
					)
					:"", 
				
				(this.props.search.total==0)?(
					React.createElement("div", {className: "filter-no-results"}, 
						React.createElement("br", null), React.createElement(Message, {message: "layers.noResults"})
					)
					):(
					React.createElement("div", {className: "esri-result-list"}, 
						
							this.props.search.results.map(function(record){
								return( React.createElement(ResultRecord, React.__spread({onAddLayer: this.props.onAddLayer, token: this.props.token},    record))
							)}.bind(this)), 
						
						(this.props.search.nextStart>-1)?(
							React.createElement("div", {className: "esri-result-item"}, 
								React.createElement("button", {className: "btn btn-apply", onClick: this.props.onNextPage}, React.createElement(Message, {message: "layers.loadMoreResults"}))
							)):""
						
					)
					)
				
							
			)
		);
	}

});
