var React = require('react');

var AllNoneSelector = React.createClass({displayName: "AllNoneSelector",

    _onAllSelected:function(){
        this.props.onSelectAll();
    },
    
    _nonNoneSelected:function(){
      this.props.onSelectNone();
    },

    render: function() {
        return(
              React.createElement("div", {className: "filter-selectors"}, 
                  React.createElement("span", null, React.createElement("a", {href: "#", onClick: this._onAllSelected}, 
                    React.createElement(Message, {message: "filters.selectAll"})
                  )), " / ", React.createElement("span", null, React.createElement("a", {href: "#", onClick: this._nonNoneSelected}, 
                    React.createElement(Message, {message: "filters.deselectAll"})
                  ))
              )
            );
    }
});

module.exports = AllNoneSelector;


