/*http://facebook.github.io/react/docs/component-specs.html*/
var React = require('react');
var Reflux = require('reflux');

var LegendStore=require('../../../../stores/esriLayesLegendsStore.js');
var DataLegendStore=require('../../../../stores/dataLayersLegendsStore.js');

var LegendActions=require('../../../../actions/legendActions.js');
var If=require('../../../commons/if.jsx')
var Legend=require('./_legend.jsx');

var _=require('lodash');

var LegendControl  = React.createClass({displayName: "LegendControl",
  mixins: [Reflux.connect(LegendStore,'esri'),Reflux.connect(DataLegendStore,'data')],

  getInitialState: function() {
    this.state = this.state || {'shown': false};
    return this.state;
  },

  _toggleVisibility: function(){
    LegendActions.isShown(!this.state.esri.shown);
  },

  render: function() {
    var esri=_.clone(this.state.esri.layersLegends) || [];
    var data =this.state.data.layersLegends || [];
    var legends=data.concat(esri);  
    
    var legendContent = "";
    var buttonLabel = this.state.esri.shown? React.createElement(Message, {message: "layers.hideLegend"}) : React.createElement(Message, {message: "layers.showLegend"});
    
    return (
      React.createElement("div", {className: "legends-container"}, 
        React.createElement("button", {className: "show-legends-button", onClick: this._toggleVisibility}, buttonLabel), 
        React.createElement(If, {condition: this.state.esri.shown}, 
          React.createElement("div", {className: "legends-content"}, 
          
            legends.map(function(l){
              return (React.createElement(Legend, {legendGroups: l.legendGroups, layerTitle: l.layerTitle, visible: l.visible}));
            })
          
          )
        )
      )
      );

  }
});

module.exports = LegendControl;
