/*http://facebook.github.io/react/docs/component-specs.html*/
var React = require('react');
var Reflux = require('reflux');

var FilterMap = require('../../conf/filterMap.js');

var LanStore=require('../../stores/lanStore.js');

var Filter=require('./filter.jsx');

var _=require('lodash');
var Message=require('.../../../commons/message.jsx');

var actions=require('../../actions/filterActions.js');
var Store=require('../../stores/filters/filterStore.js');

module.exports = React.createClass({displayName: "exports",
  mixins: [Reflux.connect(LanStore, 'lan')],

  _applyFilter:function(){
    actions.applyFilters();
  },

  _resetFilter:function(){
    for (var key in actions) {
      if (actions.hasOwnProperty(key)) {
        if (actions[key].clean){
          actions[key].clean();
        }
      }
    }
    actions.applyFilters();
  },

  _turnAdvancedOn: function() {
    this.setState({
      mode: 'advanced'
    });
  },

  _turnBasicOn: function() {
    this.setState({
      mode: 'basic'
    });
  },

  _onActivateFilter: function(index,evt) {
    this.setState(_.assign({
      activeIndex: index
    }));
  },

  _onChangeCounter: function(param, selected, total) {
    var counters = _.clone(this.state.counters);
    counters[param] = {"selected": selected, "total": total};
    this.setState({"counters": counters});
  },

  getInitialState: function() {
    return {
      counters: {},
      activeIndex: 1,
      mode: 'basic'
    };
  },

  render: function() {
  var filters = _.sortBy(FilterMap.filters, 'order');
  var idx = 1;
  return(
    React.createElement("div", {className: "activity-nav"}, 
      React.createElement("div", {className: "tab-pane "}, 
        React.createElement("div", {className: "activity-nav"}, 
          React.createElement("nav", {className: "activities"}, 
            React.createElement("ul", {className: "activities nav nav-tabs"}, 
              
                _.filter(filters,function(it){return (it.modes.indexOf(this.state.mode) > -1)}.bind(this)).map(function(def){
                  return (
                    React.createElement("li", {onClick: this._onActivateFilter.bind(null,def.index), className: (this.state.activeIndex==def.index)?'active':''}, 
                      React.createElement("a", {href: "#"}, 
                        React.createElement(Message, {message: def.label}), 
                        def.param?
                          this.state.counters[def.param]? " ("+this.state.counters[def.param].selected+"/"+this.state.counters[def.param].total+")" : ""
                        : ""
                      )
                    )
                    )
                }.bind(this)), 
              
              React.createElement("li", null, 
                React.createElement("div", {className: "filters-more", onClick: this.state.mode=="basic"? this._turnAdvancedOn : this._turnBasicOn}, 
                  this.state.mode=="basic"? React.createElement(Message, {message: "filters.moreFilters"}) : React.createElement(Message, {message: "filters.lessFilters"})
                )
              )
            )
          ), 
          React.createElement("div", null, 
            
              filters.map(function(def){
                return ( React.createElement(Filter, React.__spread({},  def, {onChangeCounter: this._onChangeCounter, active: def.index==this.state.activeIndex})))
              }.bind(this))
            
          )
        )
      ), 
      React.createElement("div", {className: "button-pane"}, 
        React.createElement("button", {type: "button", className: "btn btn-apply", role: "button", onClick: this._resetFilter}, React.createElement(Message, {message: "filters.reset"})), 
        React.createElement("button", {type: "button", className: "btn btn-apply space-left", role: "button", onClick: this._applyFilter}, React.createElement(Message, {message: "filters.apply"}))
      )
    )
    );
  }

});

