														React.createElement("td", {className: "description"}, indicator.description), 
														React.createElement("td", null, React.createElement(Button, {onClick: this._changeIndicator.bind(null,indicator), bsStyle: "primary", bsSize: "xsmall"}, React.createElement(Message, {message: "layers.select"})))
